.button-wrapper {
  display: flex;
}

.modal-button {
  width: 136px;
  height: 44px;
  border-radius: 5px;

  text-transform: uppercase;
  align-items: center;
  position: relative;
  overflow: hidden;
  text-align: center;
  color: #000000;
  border: 1px solid #000000;
  background-color: transparent;
  cursor: pointer;
  transition: All 250ms cubic-bezier(0.4, 0, 0.2, 1);

  &:not(:last-child) {
    margin-right: 15px;
  }
  &:active {
    top: 0.3em;
    left: 0.3em;
    box-shadow: 0 0 0 60px rgba(0, 0, 0, 0.05) inset;
  }
  &::before {
    content: '';
    height: 100px;
    position: absolute;
    transform: translate3d(-120%, -50px, 0) rotate3d(0, 0, 1, 45deg);
    transition: all 1.1s cubic-bezier(0.19, 1, 0.22, 1);
    width: 200px;
  }
  &:hover {
    color: #ffffff;
    background-color: #ff6b01;
    border-color: transparent;
    box-shadow: 0 0 5px rgba(255, 245, 245, 0.8);
  }
  &:hover::before {
    background-color: #ffffff;
    transform: translate3d(100%, -100px, 0) rotate3d(0, 0, 1, 90deg);
  }
}

@media screen and (max-width: 1023px) {
  .modal-button {
    width: 125.03px;
    &:not(:last-child) {
      margin-right: 14.94px;
    }
  }
}
@media screen and (max-width: 767px) {
  .modal-button {
    width: 112.58px;
    &:not(:last-child) {
      margin-right: 14.85px;
    }
    &-watched {
      padding: 0 11px;
    }
  }
}
